@import "abstracts/variables"
      , "abstracts/functions"
      , "abstracts/mixins";

/*@import "bases/debug";*/

body::before {
  background-color: rgba(0, 0, 0, .8);
  bottom: 0;
  color: #fff;
  content: var(--size);
  font-family: monospace;
  font-size: .75rem;
  padding: .25em .5em;
  position: fixed;
  right: 0;
  z-index: 101;
}

@media #{map-get($breakpoints, 'XXL')} { body { --size: "@XXL ≥ 1153px"; } }
@media #{map-get($breakpoints, 'XL')}  { body { --size: "865px ≥ @XL ≥ 1152px"; } }
@media #{map-get($breakpoints, 'L')}   { body { --size: "576px ≥ @L ≥ 864px"; } }
@media #{map-get($breakpoints, 'M')}   { body { --size: "385px ≥ @M ≥ 575px"; } }
@media #{map-get($breakpoints, 'S')}   { body { --size: "320px ≥ @S ≥ 384px"; } }
@media #{map-get($breakpoints, '0')}   { body { --size: "OUT!"; } }



[grid-debug] {
  background-image: linear-gradient(to right, transparent #{$grid-columngap}, rgba(250, 0, 150, .2) #{$grid-columngap}) !important;
  background-position: (-0.5 * $grid-columngap) 0 !important;
  background-size: calc((100% + #{$grid-columngap}) * 1 / 12) !important;
  background-clip: content-box !important;
}
