$base-maxwidth: 1140px;

$base-color: #4a4a4a;
$base-bgcolor: #fff;

$base-font: 1em/1.875em Roboto, sans-serif;
$base-fontsize: 16px;
$base-lineheight: 1.875em;
$base-fontfamily: Roboto, sans-serif;


$grid-columngap: 12px;
$grid-rowgap: 1.875rem;

$grid-outer-mobile-padding: 20px;

$color-blue: #06205b;
$color-lightblue: #99bbcf;


/* Colors */
$prussian-blue: #00205B;
$blue-bayoux: #505D73;
$rock-blue: #99BBCF;
$pacific-blue: #00AEC7;
$catskill-white: #EFF5F7;
$red-violet: #DD157B;
$alto-gray: #D8D8D8; // rgb(216,216,216)
$gull-gray: #9AA5B8; // rgb(154,165,184)


$shark: #2E3237;
$tundora: #4A4A4A;
$alto: #D8D8D8;

$media-max-M: 575px;
$media-max-L: 864px;
$media-max-XL: 1152px;

$breakpoints: (
    'XXL': '(min-width: 1153px)',
    'XL': '(min-width: 865px) and (max-width: 1152px)',
    'L': '(min-width: 576px) and (max-width: 864px)',
    'M': '(min-width: 385px) and (max-width: 575px)',
    'S': '(min-width: 320px) and (max-width: 384px)',
    '0': '(max-width: 319px)'
);
